<script>
  import { onMount } from 'svelte';
  import { forEach } from 'lodash-es';
  import SortingItem from './SortingItem.svelte'

  export let items = [];
  export let activeId = '';
  export let activeDirection = '';

  let prevId = '';

  // function setIdAndDirection(id) {
  //   if (id === prevId) {
  //     activeDirection = activeDirection === 'desc' ? 'asc' : 'desc';
  //   } else {
  //     for(let d of items) {
  //       if(d.id === id) {
  //         activeId = d.id;
  //         activeDirection = d.direction; 
  //       } 
  //       // if (d.id === prevId && prevDirection !== '' && d.direction !== prevDirection) {
  //       //   d.direction = prevDirection;
  //       // }
  //     }
  //     prevId = activeId;
  //     prevDirection = activeDirection;
  //   }
  // }

  function setIdAndDirection(id) {
    for(let d of items) {
      if(d.id === id) {
        if (id === prevId) {
          activeDirection = activeDirection === 'desc' ? 'asc' : 'desc';
          d.direction = activeDirection;
        } else {
          activeId = d.id;
          activeDirection = d.direction; 
        }
      } 
    }
    prevId = activeId;
  }

  onMount(() => {
    setIdAndDirection(activeId);
  })
</script>

<style lang="scss">/* breaks */
.sorting {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 960px) {
  .sorting {
    flex-direction: row;
  }
