/*# sourceMappingURL=src/components/ui/Sorting.svelte.map */</style>

<div class="sorting">
  {#each items as {id, label, direction} (id)}
    <SortingItem isActive={id === activeId} direction={id === activeId ? activeDirection : direction} on:click={() => setIdAndDirection(id)}
    >{label}</SortingItem>
  {/each}
</div>