  border-right: 1px solid #ccc;
}

.list__content {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: auto;
  width: 100%;
  box-sizing: border-box;
}

.list__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.list__header-title {
  display: flex;
  flex-direction: row;
}
.list__header-title h2 {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  font-family: "Nunito Sans Bold", sans-serif;
  margin-right: 6.372325899px;
}
@media (min-width: 320px) {
  .list__header-title h2 {
    margin-right: calc(6.372325899px + -0.446399973 * (100vw - 320px) / 880 );
  }
}
@media (min-width: 1200px) {
  .list__header-title h2 {
    margin-right: 5.9259259259px;
  }
}
.list__header-title h2 .filter-icon {
  display: flex;
  margin-right: 4px;
  color: #000;
}
.list__header-title span {
  color: #666;
}
/*# sourceMappingURL=src/components/ui/List.svelte.map */</style>

<div class="list" class:has-sideborder="{sideBorder}">
  <SubHeader>
    <div class="list__header">
      <div class="list__header-title">
        <h2>
          <span class="filter-icon"
            >{#if showFilterIcon}
              <Icon name="filter" />
            {/if}</span>
          {title}
        </h2>
        <span>{items.length}</span>
      </div>
      {#if sortItems.length !== 0}
        <Sorting
          items="{sortItems}"
          bind:activeId="{sortingBy}"
          bind:activeDirection="{sortingDirection}" />
      {/if}
    </div>
  </SubHeader>
  <div class="list__content">
    {#each sortedItems as d}
      {#if type === 'list'}
        <ListItem
          name="{d.name}"
          count="{d.count}"
          isActive="{d.isActive}"
          showDistribution="{showDistribution}"
          distributionScale="{distributionScale}"
          on:click="{() => {
            dispatch('click', d.id);
          }}" />
      {:else if type === 'actor'}
        <ActorItem
          text="{d.nameHtml}"
          categories="{d.categories}"
          geographies="{d.geographies}"
          hasPadding
          on:click-badge />
      {/if}
    {/each}
  </div>
</div>
