<div class="about">
  <div class="about__col">
    <h2>What is this?</h2>
    <p>
      This database maps intersections between the key social justice and human
      rights areas of our time and documented harmful AI impacts and their
      manifestations in society. Further, we mapped examples of civil society
      organizations, social movements, researchers and other entities actively
      doing work at these intersections, or who could be engaged on these issues
      via partnerships. This research is part of a broader
      <a
        href="https://foundation.mozilla.org/en/insights/movement-partnerships-analysis/"
        target="_blank">
        landscape analysis on Mozilla Foundation’s potential movement
        partnerships at the intersection of consumer AI and social justice</a
      >. As for now the analysis, as well as this database are internal and
      password-protected.
    </p>
  </div>

  <div class="about__col">
    <h2>Contribute!</h2>
    <p>
      The theoretical scope of the intersections mapping is enormous and
      certainly calls for the mapping to be expanded and adjusted over time.
      We’d like to invite Mozilla staff to contribute to the mapping by
      submitting issues or actors
      <a
        href="https://docs.google.com/forms/d/e/1FAIpQLSfWa-Recm6CA2k2a9TMF3Vpc_EStpFYdgZxve8s2QwpJb28zg/viewform?usp=sf_link"
        target="_blank">
        via this form</a
      >. Ideas for improvement or bug reports are welcome, too!
    </p>
  </div>

  <div class="about__col">
    <h2>Who worked on this?</h2>
    <p>
      The analysis and mapping were commissioned and guided by Mozilla
      Foundations's
      <a href="https://foundation.mozilla.org/en/insights/" target="_blank">
        Insights team</a
      >, in collaboration with the
      <a href="https://foundation.mozilla.org/en/what-we-fund/" target="_blank">
        Fellowship &amp; Awards team</a
      >. The research was executed by a team of freelance researchers led by
      <a href="https://uk.linkedin.com/in/jacqui-howard" target="_blank">
        Jacqui Howard</a
      >. The mapping was conducted by
      <a href="https://www.mattstempeck.com/" target="_blank">Matt Stempeck</a>
      with support from
      <a href="https://twitter.com/aniphoebe" target="_blank">Ani Hao</a>
      and
      <a href="https://www.mozillapulse.org/profile/2006" target="_blank">
        Chenai Chair</a
      >. Copy editing was done by Rosemary Maguire and Eeva Moore.
      <a href="https://twitter.com/kaodro" target="_blank">Kasia Odrozek</a>
      provided project guidance on Mozilla’s side, with support from
      <a href="http://www.linkedin.com/in/amyraikar" target="_blank">
        Amy Schapiro Raikar</a
      >. The database was designed by
      <a href="https://christianlaesser.com/" target="_blank">
        Christian Laesser</a
      >.
    </p>
    <p>
      The full list of contributors who provided ideas can be found in
      <a
        href="https://foundation.mozilla.org/en/insights/movement-partnerships-analysis/"
        target="_blank">the analysis.</a>
      Contact us at
      <a href="mailto:internethealth@mozillafoundation.org" target="_blank">
        internethealth@mozillafoundation.org</a
      >.
    </p>
  </div>
  <div class="about__col">
    <h2>Scope</h2>
    <p>
      In this version of the mapping we focused on the AI consumer space (as
      defined in the
      <a
        href="https://foundation.mozilla.org/en/insights/trustworthy-ai-whitepaper/"
        target="_blank">
        &quot;Trustworthy AI&quot; white paper</a>
      ) and traditional social justice areas, as well as Human Rights and an area
      we defined as "Community justice." These additions allowed us to capture the
      important instances that didn't fit in the other areas but seemed relevant
      for the scope. The geographical focus of the database is limited to countries
      and regions where Mozilla has an existing community, relationship, or investments
      (for the full description of the geographical scope,
      <a
        href="https://foundation.mozilla.org/en/insights/movement-partnerships-analysis/"
        target="_blank">
        "Methodology"</a> section in the analysis).
    </p>
  </div>
  <div class="about__col">
    <h2>Methodology</h2>
    <p>
      Desk research informed an understanding of the key issues at the
      intersection of AI’s impact on society and social justice. To offer
      insight and guidance on questions of global contexts and specificity, we
      interviewed Fellows, existing and potential partners, and experts, and
      received input from peer reviewers and experts. The full list of
      contributors can be found in
      <a
        href="https://foundation.mozilla.org/en/insights/movement-partnerships-analysis/"
        target="_blank">
        the analysis</a
      >.
    </p>
  </div>

  <div class="about__col">
    <h2>Definitions</h2>
    <p>
      For the purposes of this database, we defined the categories we are using
      as follows:
    </p>
  </div>
  <div class="about__col about__inline">
    <h3>Justice Area</h3>
    <p>
      as a way to classify and understand social justice issues in relation to
      an aspect of society where inequality can occur as a result of unjust
      systems, prejudices, or policies.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Impact</h3>
    <p>
      as a short description of the harm caused by AI, or work in a given
      justice area.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Issue</h3>
    <p>
      as a description on how a given (harmful) impact of AI manifests in
      society
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Actor</h3>
    <p>
      as an entity in the space actively engaging with issues at the
      intersection of AI harms and social justice, or an entity active in the
      social justice area that could be engaged on the intersectional issues. In
      this database these include civil society organizations, social movements,
      academics and researchers, government bodies, industry representatives,
      and funders.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Geography</h3>
    <p>
      as the region where the issues are relevant or actors are active. Within
      the scope of this research these include: Brazil (to a limited degree),
      Europe (with an emphasis on Germany and the UK), India, Kenya, South
      Africa, and the USA. Occasional other geographies we came across and found
      compelling enough to map are classified as "Other."
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Human Rights</h3>
    <p>
      Human Rights in the spirit of the human rights framework introduced by the
      <a
        href="https://www.un.org/en/universal-declaration-human-rights/"
        target="_blank">
        1948 Universal Declaration on Human Rights</a> and designed to be a "legally,
      politically and morally binding set of principles" that recognizes the rights
      of all people.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Racial Justice</h3>
    <p>
      in line with how
      <a href="https://www.raceforward.org/" target="_blank">
        The Center for Racial Justice Innovation</a>
      (US) defines racial justice as "the systematic fair treatment of people of
      all races, resulting in equitable opportunities and outcomes for all".
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Economic Justice</h3>
    <p>
      in line with how
      <a href="https://www.cesr.org/" target="_blank">
        The Center for Social and Economic Justice</a>
      (US) defines economic justice as a justice concept which touches the individual
      person as well as the social order, encompasses the moral principles which
      guide us in designing our economic institutions. The ultimate purpose of economic
      justice is to free each person to engage creatively in the unlimited work beyond
      economics, that of the mind and the spirit.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Gender Justice</h3>
    <p>
      in line with how
      <a href="https://www.globalfundforwomen.org/" target="_blank">
        The Global Fund for Women</a>
      (Global) defines gender justice as the systemic redistribution of power, opportunities,
      and access for people of all genders through the dismantling of harmful structures
      including patriarchy, homophobia, and transphobia.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Climate and Environmental Justice</h3>
    <p>
      as the distribution of environmental goods between nation-states but also
      about how such goods continue to be distributed at national and local
      levels under conditions of climatic change, as well as the importance of
      recognition and participation. In the Global South, it is not only about
      environmental justice but also an uneven development process.
    </p>
  </div>

  <div class="about__col about__inline">
    <h3>Community Justice</h3>
    <p>
      as issues related to individual freedoms and collective security,
      well-being, and safety.
    </p>
  </div>
</div>
