<script>
  import SubHeader from './SubHeader.svelte';
  import ModeTabs from './ModeTabs.svelte';
  import IssueItem from './IssueItem.svelte';
  import ActorItem from './ActorItem.svelte';

  export let name = '';
  export let actors = [];
  export let issues = [];

  $: actorsLastIndex = actors.length - 1;
  $: issuesLastIndex = issues.length - 1;

  let modeId = 'issues';
  $: modeOptions = [
    {
      id: 'issues',
      label: `Issue${issues.length > 1 ? 's' : ''}`,
      info: issues.length,
    },
    {
      id: 'actors',
      label: `Actor${actors.length > 1 ? 's' : ''}`,
      info: actors.length,
    },
  ];
</script>

<style lang="scss">/* breaks */
.impact {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  box-shadow: -3px 0 6px rgba(0, 0, 0, 0.12);
  z-index: 10;
}

.impact__nav {
  background-color: #f2f2f2;
}

.impact__content {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: auto;
  width: 100%;
  box-sizing: border-box;
  padding: 10.7692307692px;
}
@media (min-width: 320px) {
  .impact__content {
    padding: calc(10.7692307692px + 2.5641025641 * (100vw - 320px) / 880 );
  }
}
@media (min-width: 1200px) {
  .impact__content {
    padding: 13.3333333333px;
  }
}

.impact__header {
  font-family: "Nunito Sans Bold", sans-serif;
